import React, { useState, useEffect } from 'react';
import PostService from '../../services/PostService';
import useInterval from 'use-interval'
import { Creator } from '../../types';

const TagList: React.FC = () => {
    const [tags, setTags] = useState<Creator[]>([]);
    const [isOpen, setIsOpen] = useState<boolean>(true);

    const getTags = () => {
        setTags(PostService.getCreators());
    }

    useEffect(() => {
        getTags();
    }, []);

    useInterval(() => {
        getTags();
    }, 1000);

    const removeTag = async (tag:Creator) => {
        console.log('removing tag ' + tag.name)
        PostService.removeCreator(tag);
        getTags()
    }

    //const downloadTags = () => {
    //    const element = document.createElement('a');
    //    const file = new Blob([PostService.getTags().join('\n')], {type: 'text/plain'});
    //    element.href = URL.createObjectURL(file);
    //    element.download = 'tags.txt';
    //    document.body.appendChild(element);
    //    element.click()
    //}

    const toggleOpen = (event:any) => {
        event.preventDefault();
        setIsOpen(isOpen === false)
    }

    return (
        <>
            <table>
                <thead>
                    <tr>
                        <th>Creator</th>
                        <th><a href='#' onClick={toggleOpen} className='highlight-link'>{isOpen ? '' : ''}</a></th>
                    </tr>
                </thead>
                <tbody style={{display: isOpen ? '' : 'none'}}>
                    {tags && 
                        tags.map((tag, index) => (
                            <tr key={tag.name+index}>
                                <td><a className='tag-list-item' id={tag.id} href={`https://kemono.party/${tag.service}/user/${tag.id}`} target='_blank'>{`[${tag.name}]`}</a></td>
                                <td><a className='tag-list-item' href='#' onClick={(event) => {event.preventDefault(); if(window.confirm(`Are you sure you want "${tag.name}" removed?`)) removeTag(tag)}}>X</a></td>
                            </tr>
                        ))}
                </tbody>
            </table>
            {/*<button onClick={downloadTags}>Download Tags</button>*/}
            {/*<p><Link to='/kemono/add-multiple-tags'>Add multiple tags</Link></p>*/}
        </>
    )
}

export default TagList