import axios from "axios";
import { Post, Creator } from "../types";
import { Attachment } from "../types";

const devMode = true;

const aincludes = (at:Attachment[], a:Attachment) => {
    for(let aa of at){
        if(aa.path === a.path){
            return true;
        }
    }
    return false;
}

const getPosts = async (page:number, creators:Creator[], func:Function|null = null) => {
    const o = page * 50
    let _posts:Post[] = []
    for(let creator of creators){
        if(func){
            func(creator, creators)
        }
        let url = `https://api.codetabs.com/v1/proxy/?quest=https://kemono.party/api/${creator.service}/user/${creator.id}?o=${o}`
        let res = await axios.get<Post[]>(url)
        _posts = [..._posts, ...res.data]
    }
    let posts = []
    for(let p of _posts){
        const extensions = ['png', 'jpg', 'jpeg', 'gif']
        let _attachments = p.attachments.filter(e => {
            const split = e.path.split('.')
            const extension = split[split.length-1]
            return extensions.includes(extension)
        })
        if (p.file.path){
            const split = p.file.path.split('.')
            if(extensions.includes(split[split.length-1]) && !aincludes(_attachments, p.file)){
                _attachments = [p.file, ..._attachments]
            }
        }
        if(_attachments.length !== 0){
            p.attachments = _attachments
            posts.push(p)
        }
    }
    return posts;
}

const addCreator = (creator:Creator) => {
    let creators = getCreators()
    creators.push(creator)
    localStorage.setItem('creators', JSON.stringify(creators))
};

const addCreators = (cs:Creator[]) => {
    let creators = [...getCreators(), ...cs]
    localStorage.setItem('creators', JSON.stringify(creators))
};

const removeAllCreators = () => {
    localStorage.setItem('creators', JSON.stringify([]))
}

const removeCreator = (creator:Creator) => {
    const indexOfCreator = (c:Creator, cs:Creator[]) => {
        for(let i = 0;i<cs.length;i++){
            if(c.id === cs[i].id){
                return i
            }
        }
        return null;
    }
    let creators = getCreators();
    const index = indexOfCreator(creator, creators)
    if(index || index === 0){
        const splice = creators.splice(index, 1)
        devMode && console.log(splice);
        localStorage.setItem('creators', JSON.stringify(creators))
        if (creators.length === 0) {
            removeAllCreators()
        }
    }
}

const getCreators = ():Creator[] => {
    const creators:Creator[] = JSON.parse(localStorage.getItem('creators')!) ?? []
    if (!creators){
        return [];
    } else {
        return creators;
    }
}

const getCreatorIds = ():string[] => {
    let result = []
    for(let creator of getCreators()){
        result.push(creator.id)
    }
    return result
}

const getCreatorFromId = (id:string):Creator|null => {
    for(let c of getCreators()){
        if(c.id === id){
            return c;
        }
    }
    return null
}


const PostService = {
    addCreator,
    removeAllCreators,
    getCreators,
    removeCreator,
    getPosts,
    getCreatorIds,
    getCreatorFromId,
    addCreators
}

export default PostService