import React, {useState} from 'react';
import PostOverview from '../post-overview';
import Profile from '../profile';

const MainView: React.FC = () => {
    const [location, setLocation] = useState<string>("overview");

    return (
        <>
            {location === "overview" && <PostOverview setLocation={setLocation}/>}
            {location === "profile" && <Profile setLocation={setLocation}/>}
            {/*<Routes>
                <Route path='/kemono' element={<PostOverview/>}/>
                {<Route path='/kemono/add-multiple-tags' element={<AddTags.AddMultipleTags/>}/>}
                <Route path='/kemono/profile' element={<Profile/>}/>
            </Routes>*/}
        </>
    )
}

export default MainView