import React, { useState } from 'react';
import PostService from '../../services/PostService';

type Props = {
    refreshPosts:any;
}

const AddTags: React.FC<Props> = ({ refreshPosts }:Props) => {
    const [IdInput, setIdInput] = useState<string>('');
    const [NameInput, setNameInput] = useState<string>('');
    const [ServiceInput, setServiceInput] = useState<string>('');
    const [statusMessage, setStatusMessage] = useState<string>('');

    const handleSubmit = async (event:any) => {
        event.preventDefault();
        if (IdInput.trim() === '' || NameInput.trim() === '' || ServiceInput.trim() === '') {
            setStatusMessage('No tag given')
        } else {
            PostService.addCreator({service: ServiceInput, name: NameInput, id: IdInput});
            setStatusMessage('Tag added');
            setIdInput('');
            setNameInput('');
            setServiceInput('');
            refreshPosts();
        }
    }

    const removeAllTags = () => {
        PostService.removeAllCreators();
        setStatusMessage('All creators removed');
        refreshPosts();
    }

    return(
        <>
            <p>{statusMessage}</p>
            <form onSubmit={handleSubmit}>
                <label>
                    ID:
                    <input type='text' value={IdInput} onChange={(event) => setIdInput(event.target.value)}/>
                </label>
                <br/>
                <label>
                    Name:
                    <input type='text' value={NameInput} onChange={(event) => setNameInput(event.target.value)}/>
                </label>
                <br/>
                <label>
                    Service:
                    <input type='text' value={ServiceInput} onChange={(event) => setServiceInput(event.target.value)}/>
                </label>
                <br/>
                <input type='submit' value='Submit'/>
            </form>
            <p><a href='#' onClick={() => {if(window.confirm('Are you sure you want to remove all creators?')) removeAllTags()}}>Remove all creators</a></p>
        </>
    )
}
export default { AddTags };