import React, { useState, useEffect } from 'react';
import PostService from '../../services/PostService';
import { Attachment, Creator, Post } from '../../types';
import InfiniteScroll from 'react-infinite-scroller';
import TagList from '../tag-list';
import addTags from '../add-tags';
import Lightbox from "react-image-lightbox";
import "react-image-lightbox/style.css";
import Modal from 'react-modal'
import { Button } from '@mui/material';
import Image from './image';

const devMode = true;

type Props = {
    setLocation:Function
}

const PostOverview: React.FC<Props> = ({setLocation}:Props) => {
    function useSessionStorage<T> (storageKey:string, fallbackState:T) : [T, React.Dispatch<React.SetStateAction<T>>] {
        const [value, setValue] = useState<T>(JSON.parse(sessionStorage.getItem(storageKey)!) ?? fallbackState)
        useEffect(() => {
            sessionStorage.setItem(storageKey, JSON.stringify(value))
        }, [value, storageKey])
        return [value, setValue]
    }
    function useLocalStorage<T> (storageKey:string, fallbackState:T) : [T, React.Dispatch<React.SetStateAction<T>>] {
        const [value, setValue] = useState<T>(JSON.parse(localStorage.getItem(storageKey)!) ?? fallbackState)
        useEffect(() => {
            localStorage.setItem(storageKey, JSON.stringify(value))
        }, [value, storageKey])
        return [value, setValue]
    }

    const [posts, setPosts] = useSessionStorage<Post[]>("posts", []);
    const [page, setPage] = useSessionStorage<number>("page", 0);
    const [isLoading, setLoadingState] = useState<boolean>(false);
    const [loadMessage, setLoadMessage] = useState<string>('Load more ...');
    const [imageViewerIsOpened, setImageViewerIsOpened] = useState<boolean>(false);
    const [videoIsOpen, setVideoIsOpen] = useState<boolean>(false);
    const [viewedImage, setViewedImage] = useState<Post|undefined>(undefined);
    const [imagesPerRow, setImagesPerRow] = useLocalStorage<number>("images-per-row", 3);
    const [showAllArtists, setShowAllArtists] = useState<boolean>(true);
    const [isPoolOpened, setPoolOpened] = useState<boolean>(false);
    const [poolAttachments, setPoolAttachments] = useState<Attachment[]>([])
    const [prevAndNextImage, setPrevAndNextImage] = useState<{prev:Post|undefined, next:Post|undefined}>({prev: undefined, next: undefined});
    const [poolImageSize, setPoolImageSize] = useLocalStorage<number>("pool-image-size", 100);
    const [screenWidth, setScreenWidth] = useState<number>(1000);
    const [nextPosts, setNextPosts] = useSessionStorage<Post[]>("nextposts", []);
    const [poolImages, setPoolImages] = useState<{prev:Attachment|undefined, next:Attachment|undefined, current:Attachment|undefined}>({prev: undefined, next: undefined, current: undefined});
    const [poolImageViewerIsOpened, setPoolImageViewerIsOpened] = useState<boolean>(false);

    useEffect(() => {
        devMode && console.log('I fire once')
        window.addEventListener('resize', () => setScreenWidth(window.innerWidth))
        if (PostService.getCreators().length === 0){
            window.alert("Seems like you don't follow anyone. Either manually add Creators using the form on the left or upload a previously created profile in \"Profile\"."+
            "\n\nWhen adding a creator, the name can be whatever you choose, the ID and service you can find in the URL when looking at their kemono page."+
            "\n\nhttps://kemono.party/<service>/user/<ID>/")
        }
    }, []);

    useEffect(() => {
        document.body.style.overflow = imageViewerIsOpened || isPoolOpened || videoIsOpen ? 'hidden' : 'unset';
    }, [imageViewerIsOpened, isPoolOpened, videoIsOpen])

    const loadMessageFormatter = (creator:Creator, creators:Creator[]) => {
        let string = `Getting ${creator.name} (${creators.indexOf(creator)+1}/${creators.length})`;
        setLoadMessage(string);
    }

    const showPool = async (event:any, attachments:Attachment[]) => {
        event.preventDefault();
        setPoolAttachments([])
        setPoolOpened(true);
        setPoolAttachments(attachments)
    }

    const getPosts = async () => {
        if(!isLoading){
            setLoadingState(true)
            let local_nextPosts = nextPosts;
            const post_amount = 20;
            if (local_nextPosts.length < post_amount){
                const incomingPosts = await getNextPosts();
                local_nextPosts = [...local_nextPosts, ...incomingPosts];
            }
            const postSection = local_nextPosts.slice(0, post_amount);
            local_nextPosts.splice(0, post_amount);
            setNextPosts(local_nextPosts);
            const local_posts = [...posts, ...postSection]
            setPosts(local_posts);

            setTimeout(() => setLoadingState(false), 3000);
        }
    }

    const getNextPosts = async () => {
        devMode && console.log("Loading posts ...")

        const creators = PostService.getCreators()
        if (creators.length === 0){
            return []
        }
        let uniquePosts = await PostService.getPosts(page, creators, loadMessageFormatter)

        devMode && console.log('sorting ...')
        uniquePosts.sort((a:Post, b:Post) => {
            let left = new Date(a.added);
            let right = new Date(b.added);
            if(left > right) {
                return -1;
            }
            if (right > left) {
                return 1;
            }
            return 0;
        })
        setLoadMessage('Load more ...')
        const newpage = page +1
        setPage(newpage)
        return uniquePosts;
    };

    const getMore = (event:any) => {
        event.preventDefault();
        getPosts()
    }

    const refreshPosts = () => {
        devMode && console.log('Refreshing posts')
        sessionStorage.removeItem("posts")
        sessionStorage.removeItem("nextposts")
        sessionStorage.removeItem("page")
        window.location.reload()
    }

    const changeImagesPerRow = (amount:number) => {
        let newAmount = imagesPerRow + amount;
        if (newAmount > 0 && newAmount <= 10) {
            setImagesPerRow(newAmount);
        }
    }

    const imageViewer = (post:Post) => {
        setViewedImage(post);
        setVideoIsOpen(false);
        setImageViewerIsOpened(true);
        const theseposts = posts;
        const prevIndex = theseposts.indexOf(post)-1;
        const prev = prevIndex < 0 ? undefined : theseposts[prevIndex];
        const nextIndex = prevIndex+2;
        const next = nextIndex > theseposts.length ? undefined : theseposts[nextIndex];
        setPrevAndNextImage({prev: prev, next: next});
    }

    const poolImageViewer = (attachment:Attachment) => {
        setVideoIsOpen(false);
        const prevIndex = poolAttachments.indexOf(attachment)-1;
        const prev = prevIndex < 0 ? undefined : poolAttachments[prevIndex];
        const nextIndex = prevIndex+2;
        const next = nextIndex > poolAttachments.length ? undefined : poolAttachments[nextIndex];
        setPoolImages({prev: prev, next: next, current: attachment});
        setPoolImageViewerIsOpened(true);
    }

    const changePoolImageSize = (amount:number) => {
        let newAmount = poolImageSize + amount;
        if (newAmount > 0 && newAmount <= 100) {
            setPoolImageSize(newAmount);
        }
    }

    const modalStyle:Modal.Styles = {
        content: {
            backgroundColor: 'black',
            top: screenWidth <= 720 ? '0%' : '3%',
            bottom: screenWidth <= 720 ? '0%' : '3%',
            left: screenWidth <= 720 ? '0%' : '3%',
            right: screenWidth <= 720 ? '0%' : '3%'
        }
    };

    return (
        <>
            <div className='aside-grid-container'>
                <div>
                    {/*<Link to="/kemono/profile">Profile</Link>*/}
                    <div style={{display: 'flex', justifyContent: 'space-between'}}>
                        <a href='#' onClick={() => setLocation("profile")}>Profile</a>
                        <a href='#' onClick={e => {e.preventDefault(); refreshPosts()}}>Refresh posts</a>
                    </div>
                    <div style={{display: 'flex', justifyContent: 'space-around'}}>
                        <p><a href='#' onClick={() => changeImagesPerRow(-1)}>{'<<'}</a></p>
                        <p>{imagesPerRow}</p>
                        <p><a href='#' onClick={() => changeImagesPerRow(1)}>{'>>'}</a></p>
                    </div>
                    <div>
                        <input type='checkbox' id='show-artists' onChange={() => setShowAllArtists(!showAllArtists)} checked={showAllArtists} />
                        <label htmlFor='show-artists'>Always show info</label>
                    </div>
                    <addTags.AddTags refreshPosts={refreshPosts}/>
                    <TagList/>
                </div>
                <div style={{textAlign: 'center'}}>
                    <div style={{display: 'flex', flexWrap: 'wrap'}}>
                        {posts.map((post, index) => (
                            <Image post={post} onClick={() => imageViewer(post)} key={index} showAllArtists={showAllArtists} imagesPerRow={imagesPerRow} showPool={showPool}/>
                        ))}
                    </div>
                    <InfiniteScroll
                        hasMore={!isLoading}
                        loadMore={getPosts}
                    />
                    <h1><a href='#' onClick={getMore}>{loadMessage}</a></h1>
                </div>
            </div>
            {imageViewerIsOpened && <Lightbox
                mainSrc={viewedImage === undefined ? '' : `https://kemono.party${viewedImage?.file.path}`}
                mainSrcThumbnail={`https://kemono.party/thumbnail/data${viewedImage?.file.path}`}
                onCloseRequest={() => setImageViewerIsOpened(false)}
                nextSrc={prevAndNextImage.next ? `https://kemono.party${prevAndNextImage.next.file.path}` : undefined}
                nextSrcThumbnail={prevAndNextImage.next ? `https://kemono.party/thumbnail/data${prevAndNextImage.next.file.path}` : undefined}
                prevSrc={prevAndNextImage.prev ? `https://kemono.party${prevAndNextImage.prev.file.path}` : undefined}
                prevSrcThumbnail={prevAndNextImage.prev ? `https://kemono.party/thumbnail/data${prevAndNextImage.prev.file.path}` : undefined}
                onMoveNextRequest={() => {if (prevAndNextImage.next !== undefined) imageViewer(prevAndNextImage.next)}}
                onMovePrevRequest={() => {if (prevAndNextImage.prev !== undefined) imageViewer(prevAndNextImage.prev)}}
            />}
            {poolImageViewerIsOpened && <Lightbox
                mainSrc={poolImages.current ? `https://kemono.party${poolImages.current.path}` : ''}
                mainSrcThumbnail={`https://kemono.party/thumbnail/data${poolImages.current?.path}`}
                onCloseRequest={() => setPoolImageViewerIsOpened(false)}
                nextSrc={poolImages.next ? `https://kemono.party${poolImages.next.path}` : undefined}
                nextSrcThumbnail={poolImages.next ? `https://kemono.party/thumbnail/data${poolImages.next.path}` : undefined}
                prevSrc={poolImages.prev ? `https://kemono.party${poolImages.prev.path}` : undefined}
                prevSrcThumbnail={poolImages.prev ? `https://kemono.party/thumbnail/data${poolImages.prev.path}` : undefined}
                onMoveNextRequest={() => {if (poolImages.next !== undefined) poolImageViewer(poolImages.next)}}
                onMovePrevRequest={() => {if (poolImages.prev !== undefined) poolImageViewer(poolImages.prev)}}
            />}
            <Modal isOpen={isPoolOpened} onRequestClose={() => setPoolOpened(false)} ariaHideApp={false} style={modalStyle}>
                <div style={{display: 'flex', justifyContent: 'space-between'}}>
                    <div className='modal-button'>
                        <Button onClick={() => setPoolOpened(false)}>Close pool</Button>
                    </div>
                    <div style={{display: 'flex', justifyContent: 'space-around'}}>
                        <p><a href='#' onClick={(event) => {event.preventDefault(); changePoolImageSize(-5)}}>{'<<'}</a></p>
                        <p style={{padding: '0 1rem'}}>{poolImageSize}</p>
                        <p><a href='#' onClick={(event) => {event.preventDefault(); changePoolImageSize(5)}}>{'>>'}</a></p>
                    </div>
                </div>
                <div>
                    {poolAttachments.map((attachment, index) => (
                        <img width={`${poolImageSize}%`} key={index} src={`https://kemono.party/thumbnail/data${attachment.path}`} alt={String(attachment.name)} onClick={() => poolImageViewer(attachment)} />
                    ))}
                </div>
                <div className='modal-button' style={{margin: '1rem 0 0 0', display: 'flex', justifyContent: 'center'}}>
                    <Button onClick={() => setPoolOpened(false)}>Close pool</Button>
                </div>
            </Modal>
        </>
    )
}

export default PostOverview