import React, {useEffect} from 'react';
import PostService from '../services/PostService';
import { Creator } from '../types';

type Props = {
    setLocation:Function
}


const Profile: React.FC<Props> = ({setLocation}:Props) => {
    useEffect(() => {
        const oldtitle = document.title
        document.title = "Kemono - profile"
        return () => {document.title = oldtitle}
    }, [])

    const downloadProfile = () => {
        const element = document.createElement('a');
        const data = JSON.stringify(PostService.getCreators());
        const file = new Blob([data], {type: 'application/json'});
        element.href = URL.createObjectURL(file);
        element.download = 'kemono-profile.e8e';
        document.body.appendChild(element);
        element.click()
    }

    const handleFile = (e:any) => {
        const fileReader = new FileReader();
        fileReader.readAsText(e.target.files[0], "UTF-8");
        fileReader.onload = e => {
            const data:Creator[] = JSON.parse(String(e.target?.result));
            PostService.addCreators(data)
            window.location.reload();
        }
    }

    return(
        <>
            <p><a href='#' onClick={() => setLocation("overview")}>{"<< Return"}</a></p>
            <div>
                <h2>Profile</h2>
                <p>Creatoramount: {PostService.getCreators().length}</p>
                <button onClick={downloadProfile}>Save profile</button>
            </div>
            <div>
                <p>Upload profile:</p>
                <input type="file" onChange={handleFile} accept=".e8e" />
            </div>
        </>
    )
}

export default Profile;